import db from '../data/data';
import scissorsSvg from '../images/scissors.svg';
import planeSvg from '../images/plane.svg';

function createElement({ tag, className, text }) {
  const element = document.createElement(tag);
  if (className) {
    if (typeof className === 'string') {
      element.classList.add(className);
    } else {
      className.forEach((item) => {
        element.classList.add(item);
      });
    }
  }
  if (text) {
    if (typeof text === 'string') {
      const textNode = document.createTextNode(text);
      element.appendChild(textNode);
    } else {
      text.forEach((item) => {
        const textNode = document.createTextNode(item);
        element.appendChild(textNode);
        element.appendChild(createElement({ tag: 'br' }));
      });
    }
  }
  return element;
}

function populateSummary(data) {
  const summaryList = document.getElementById('summary');
  data.forEach((item) => {
    const li = createElement({ tag: 'li' });
    const span = createElement({ tag: 'span', text: item });
    li.appendChild(span);
    summaryList.appendChild(li);
  });
}

function populateTech(data) {
  const techList = document.getElementById('tech');
  data.forEach((item) => {
    const dl = createElement({ tag: 'dl' });
    const dt = createElement({ tag: 'dt', text: item.title });
    const dd = createElement({ tag: 'dd', text: item.description });
    dl.appendChild(dt);
    dl.appendChild(dd);
    techList.appendChild(dl);
  });
}

function createProject(data) {
  const project = createElement({ tag: 'div', className: 'project' });
  const readMoreCheckbox = createElement({ tag: 'input', className: 'read-more-state' });
  const readMoreId = Math.random();
  readMoreCheckbox.setAttribute('type', 'checkbox');
  readMoreCheckbox.setAttribute('id', readMoreId);

  const readMoreWrapContainer = createElement({ tag: 'div', className: 'read-more-wrap' });
  const projectName = createElement({ tag: 'div', className: 'name' });
  const projectLabel = createElement({ tag: 'span', className: 'label', text: 'Project:' });
  const projectValue = createElement({ tag: 'span', className: 'value', text: data.name });
  projectName.appendChild(projectLabel);
  projectName.appendChild(projectValue);
  const description = createElement({ tag: 'div', className: 'description', text: data.description });

  const responsibilities = createElement({ tag: 'div', className: ['responsibilities', 'read-more-target'] });
  const responsibilitiesLabel = createElement({ tag: 'span', className: 'label', text: 'Responsibilities:' });
  const responsibilitiesList = document.createElement('ul');
  data.responsibilities.forEach((responsibility) => {
    const responsibilityNode = createElement({ tag: 'li', text: responsibility });
    responsibilitiesList.appendChild(responsibilityNode);
  });
  responsibilities.appendChild(responsibilitiesLabel);
  responsibilities.appendChild(responsibilitiesList);

  const stack = createElement({ tag: 'div', className: ['stack', 'read-more-target'] });
  const stackLabel = createElement({ tag: 'span', className: 'label', text: 'Stack:' });
  const stackValue = createElement({ tag: 'span', className: 'value', text: data.stack });
  stack.appendChild(stackLabel);
  stack.appendChild(stackValue);

  const readMoreLabel = createElement({ tag: 'label', className: 'read-more-trigger' });
  readMoreLabel.setAttribute('for', readMoreId);

  readMoreWrapContainer.appendChild(projectName);
  readMoreWrapContainer.appendChild(description);
  readMoreWrapContainer.appendChild(responsibilities);
  readMoreWrapContainer.appendChild(stack);

  project.appendChild(readMoreCheckbox);
  project.appendChild(readMoreWrapContainer);
  project.appendChild(readMoreLabel);

  return project;
}

function populateExp(data) {
  const expSection = document.getElementById('exp');

  data.forEach((item) => {
    const article = document.createElement('article');

    const company = createElement({ tag: 'div', className: 'company' });
    let companyName;
    if (item.company.type === 'business-trip') {
      companyName = createElement({ tag: 'div', className: 'name' });
      const companyFrom = createElement({ tag: 'span', className: 'from', text: item.company.from });
      const plane = createElement({ tag: 'img', className: 'plane' });
      plane.setAttribute('src', planeSvg);
      plane.setAttribute('alt', 'plane');
      const companyTo = createElement({ tag: 'span', className: 'to', text: item.company.to });
      companyName.appendChild(companyFrom);
      companyName.appendChild(plane);
      companyName.appendChild(companyTo);
    } else {
      companyName = createElement({ tag: 'div', className: 'name', text: item.company.name });
    }
    const workDuration = createElement({ tag: 'div', className: 'duration', text: item.company.workDuration });
    company.appendChild(companyName);
    company.appendChild(workDuration);

    const position = createElement({ tag: 'div', className: 'position' });
    const positionLabel = createElement({ tag: 'span', className: 'label', text: 'Position:' });
    const positionValue = createElement({ tag: 'span', className: 'value', text: item.position });
    position.appendChild(positionLabel);
    position.appendChild(positionValue);

    article.appendChild(company);
    article.appendChild(position);

    item.projects.forEach((project, index, array) => {
      const projectNode = createProject(project);
      article.appendChild(projectNode);

      if (index !== array.length - 1) {
        const delimiter = createElement({ tag: 'div', className: 'delimiter' });
        const delimiterLine = createElement({ tag: 'div', className: 'delimiter-line' });
        const scissors = createElement({ tag: 'img' });
        scissors.setAttribute('src', scissorsSvg);
        scissors.setAttribute('alt', 'scissors');
        delimiter.appendChild(delimiterLine);
        delimiter.appendChild(scissors);

        article.appendChild(delimiter);
      }
    });

    expSection.appendChild(article);
  });
}

function populate(data) {
  populateSummary(data.summary);
  populateTech(data.tech);
  populateExp(data.exp);
}

populate(db);
